.Form{
    height: 90vh;
    width: 40vw;
    float: right;
    display: flex;
    text-align: center;
    align-items: center;
    margin-right: 15%;
}

.submit-btn{
animation: moveright 1.5s forwards;
}
@keyframes moveright{
    0%{ opacity: 0; transform: translateX(-300px);}
    60%{ transform: translateX(20px);}
    90%{ transform: translateX(-10px);}
    100%{ opacity: 1; transform: translateX(0);}
}